    margin: 0;
  }
  .about__video-container img {
    object-fit: cover;
    object-position: top;
  }
}
.about__video-container a::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 54, 122, 0.5);
}
.about__video-container button {
  position: absolute;
  display: flex;
  top: 50%;
  left: 50%;
  z-index: 2;
  padding: 0;
  border: none;
  background-color: transparent;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
@media (max-width: 1199px) {
  .about__video-container button svg {
    width: 73px;
    height: 73px;
  }
}
.about__video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.footer {
  margin-top: auto;
  padding: 45px 0 32px;
  background-color: #030a1a;
}
@media (max-width: 1199px) {
  .footer {
    padding: 47px 0 49px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 50px 0 52px;
  }
}

.footer .container {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer .container {
    flex-direction: column;
  }
}

@media (max-width: 767px) {
  .footer__nav {
    margin-bottom: 40px;
  }
}

.footer__nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}
@media (max-width: 1199px) {
  .footer__nav-list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .footer__nav-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.footer__nav-link {
  color: #ffffff;
  font-size: 14px;
  line-height: 16px;
}

.footer__nav-item {
  display: flex;
  align-items: center;
}

.footer__logo {
  padding: 10px 0;
  margin-right: 17.5vw;
  flex-shrink: 0;
}
@media (max-width: 1199px) {
  .footer__logo {
    margin-right: 1vw;
  }
}
@media (max-width: 1199px) {
  .footer__logo img {
    width: 82px;
    height: 18px;
  }
}
@media (max-width: 767px) {
  .footer__logo img {
    width: 68px;
    height: 15px;
  }
}

.footer__social {
  max-width: 300px;
}

.footer__social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 11px;
}

.footer__social-item {
  background-color: #ffffff;
  border-radius: 50px;
}

.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  color: #030a1a;
}

